import Link from "next/link";
import Image from "next/image";

export default function ProductCard({ product }) {
    return (
        <div className="border p-4 rounded shadow hover:shadow-lg transition transform hover:-translate-y-1">
            <div className="relative w-full h-48 mb-2">
                <Image
                    src={product.image || "/items/default.jpg"}
                    alt={product.title}
                    fill
                    className="object-cover rounded"
                />
            </div>
            <h2 className="font-bold text-lg">{product.title}</h2>
            <p className="text-gray-600 text-sm line-clamp-2">{product.shortDescription}</p>
            <p className="text-green-600 font-semibold mt-1">${product.price}</p>
            <Link
                href={`/items/${product._id}`}
                className="mt-2 inline-block text-sm text-green-700 hover:underline"
            >
                Details
            </Link>
        </div>
    );
}
